'use strict';

/**
* Decorate a provided function such that the function's return value is provided as an argument to another function.
*
* @module @stdlib/utils-decorate-after
*
* @example
* import abs from '@stdlib/math-base-special-abs';
* import decorateAfter from '@stdlib/utils-decorate-after';
*
* function negate( v ) {
*     return -v;
* }
*
* var f = decorateAfter( abs, 1, negate );
* // returns <Function>
*
* var v = f( -5 );
* // returns -5
*
* v = f( 5 );
* // returns -5
*
* @example
* import abs from '@stdlib/math-base-special-abs';
* import decorateAfter from '@stdlib/utils-decorate-after';
*
* function log( v ) {
*     console.log( v );
* }
*
* var f = decorateAfter( abs, 1, log );
* // returns <Function>
*
* var v = f( -5 );
* // returns 5
*
* v = f( 5 );
* // returns 5
*
* @example
* import abs from '@stdlib/math-base-special-abs';
* import decorateAfter from '@stdlib/utils-decorate-after';
*
* function counter() {
*     this.count += 1;
* }
*
* var ctx = {
*     'count': 0
* };
*
* var f = decorateAfter( abs, 1, counter, ctx );
* // returns <Function>
*
* var v = f( -5 );
* // returns 5
*
* v = f( 5 );
* // returns 5
*
* var count = ctx.count;
* // returns 2
*
* @example
* import abs from '@stdlib/math-base-special-abs';
* import decorateAfter from '@stdlib/utils-decorate-after';
*
* function negate( v ) {
*     return -v;
* }
*
* var f = decorateAfter.factory( abs, 1, negate );
* // returns <Function>
*
* var v = f( -5 );
* // returns -5
*
* v = f( 5 );
* // returns -5
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
